body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-header {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    text-align: center;
    background-color: #131A28;
    color: white;
    justify-content: center;
    align-items: center;
}


.heading-text {
    color: white;
    font-size: 42px;
    font-weight: bolder;
    text-align: center;
    margin: 50px;
    margin-top: 10px;
}

.player{
   color: white;
    font-size: 22px;
    font-weight: bolder;
    text-align: center;
    margin: 20px;
    margin-bottom: 5px;
}

.square {
    min-width: 80px;
    min-height: 80px;
    font-size: 64px;
    text-align: center;
    color: #1CDBF5;
    font-weight: bolder;
    padding: 30px;
}

.fc-aqua {
    color: greenyellow;
}

.fc-white {
    color: white;
}

.fw-600 {
    font-weight: bolder;
}

.row {
    display: flex;
    flex-direction: row;
}

.column {
    display: flex;
    flex-direction: column;
}

.jc-center {
    justify-content: center;
}

.b-bottom-right {
    border-right: 15px solid greenyellow;
    border-bottom: 15px solid greenyellow;
}

.b-bottom {
    border-bottom: 15px solid greenyellow;
}

.b-right {
    border-right: 15px solid greenyellow;
}

.clear-button {
    width: max-content;
    padding: 20px;
    margin: 20px auto 10px auto;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 600;
    background-color: greenyellow;
    text-transform: uppercase;
    cursor: pointer;
}


@media only screen and (max-width: 600px) {
    .heading-text {
        color: white;
        font-size: 30px;
        font-weight: bolder;
        text-align: center;
        margin: 60px 10px;
    }

    .square {
        min-width: 60px;
        min-height: 60px;
        font-size: 28px;
        font-weight: bolder;
        padding: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .b-bottom-right {
        border-right: 10px solid greenyellow;
        border-bottom: 10px solid greenyellow;
    }

    .b-bottom {
        border-bottom: 10px solid greenyellow;
    }

    .b-right {
        border-right: 10px solid greenyellow;
    }

    .clear-button {
        width: max-content;
        margin: 20px auto 10px auto;
    }
}